import { EventContext } from '@enactive-ac/api/src/domain/EventContext';
import { FindTour } from '@enactive-ac/api/src/domain/FindTour';
import {
  FindToursRequest,
  FindToursResponse,
} from '@enactive-ac/api/src/domain/FindTours';
import { PostTourActionRequest } from '@enactive-ac/api/src/domain/PostTourAction';
import { TourStepButton } from '@enactive-ac/api/src/domain/TourStepButton';
import { AxiosInstance } from 'axios';
import Mustache from 'mustache';

import { resolveEventContext } from '../domain/EnactiveMetadata';
import { RecursivePartial } from '../util/RecursivePartial';

const storage: Record<string, string> = {};

export class TourService {
  constructor(private readonly axios: AxiosInstance) {}

  public async findAvailableTours(
    eventContext: RecursivePartial<EventContext> | undefined,
  ): Promise<FindTour[]> {
    const req: FindToursRequest = {
      context: resolveEventContext(eventContext),
    };
    return (await this.axios.post<FindToursResponse>('tours', req)).data.tours;
  }

  public async customizeTourSteps(
    eventContext: RecursivePartial<EventContext>,
    tour: FindTour,
  ) {
    for (const step of tour.steps) {
      step.html = Mustache.render(step.html, { context: eventContext });
    }
  }

  public async collectTourAction(
    eventContext: RecursivePartial<EventContext> | undefined,
    tourId: string,
    stepId: string,
    action: TourStepButton['action'],
  ): Promise<void> {
    this.logTourTime();

    const context = resolveEventContext(eventContext);
    const req: PostTourActionRequest = { context };
    await this.axios.post(
      `tours/${tourId}/steps/${stepId}/actions/${action}`,
      req,
    );
  }

  private logTourTime(): void {
    storage['enactive.last-tour-time'] = new Date().toISOString();
  }

  private hasShownTourRecently(): boolean {
    const lastTourTime = storage['enactive.last-tour-time'];
    if (lastTourTime !== null) {
      // Only show one tour per hour
      return Date.now() - new Date(lastTourTime).getTime() < 60 * 60 * 1000;
    } else {
      return false;
    }
  }
}
