import type { EventContext } from '@enactive-ac/api/src/domain/EventContext';
import { JSONSchemaType } from 'ajv';

import { RecursivePartial } from '../util/RecursivePartial';
import { deepMerge } from '../util/deepMerge';

export type EnactiveMetadata = {
  apiHost: string;
  context?: RecursivePartial<EventContext>;
  enabled: boolean;
  siteId: string;
};

export function resolveEventContext(
  context: RecursivePartial<EventContext> | undefined,
): EventContext {
  return deepMerge(
    {
      device: {
        locale:
          document.querySelector('html')?.getAttribute('lang') ?? undefined,
      },
      page: {
        url: window.location.href,
      },
      user: {
        anonymous: true,
        id: '',
      },
    },
    context ?? {},
  );
}

export const EnactiveMetadataSchema: JSONSchemaType<EnactiveMetadata> = {
  properties: {
    apiHost: {
      type: 'string',
    },
    context: {
      nullable: true,
      properties: {
        course: {
          nullable: true,
          properties: {
            id: {
              nullable: false,
              type: 'string',
            },
          },
          required: ['id'],
          type: 'object',
        },
        device: {
          nullable: true,
          properties: {
            locale: {
              nullable: true,
              type: 'string',
            },
          },
          type: 'object',
        },
        page: {
          nullable: true,
          properties: {
            locale: {
              nullable: true,
              type: 'string',
            },
            url: {
              nullable: true,
              type: 'string',
            },
          },
          type: 'object',
        },
        user: {
          nullable: true,
          properties: {
            anonymous: {
              nullable: true,
              type: 'boolean',
            },
            id: {
              nullable: true,
              type: 'string',
            },
            locale: {
              nullable: true,
              type: 'string',
            },
            role: {
              nullable: true,
              type: 'string',
            },
          },
          type: 'object',
        },
      },
      type: 'object',
    },
    enabled: {
      type: 'boolean',
    },
    siteId: {
      type: 'string',
    },
  },
  required: ['apiHost', 'enabled', 'siteId'],
  type: 'object',
};
