import deepmerge from 'deepmerge';

import { RecursivePartial } from './RecursivePartial';

/**
 * Merge happens from left to right.
 * E.g., `merge({ a: 5 }, { a: 6 }) becomes { a: 6 }`
 *
 * Arrays are overwritten, not merged.
 * E.g., `merge({a: [1, 2]}, { a: [3, 4] }) becomes { a: [3, 4] }`
 */
export function deepMerge<T extends object>(
  defaultValues: T,
  ...overrides: RecursivePartial<T>[]
): T {
  return deepmerge.all([defaultValues, ...overrides], {
    arrayMerge: target => target,
  }) as unknown as T;
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
(window as unknown as any).deepMerge = deepMerge;
