import React, { useEffect, useRef, useState } from 'react';

import { setTimeout } from '../util/PromiseUtil';

export type ClickAwayListenerProps = React.PropsWithChildren<{
  onClickAway: (ev: MouseEvent) => void;
  /** Wait for this period of time before we accept click-away events. */
  waitFor?: number;
}>;

export const ClickAwayListener: React.FC<ClickAwayListenerProps> = ({
  children,
  onClickAway,
  waitFor = 3000,
}) => {
  const ref = useRef<HTMLDivElement>(null);
  const [mountedAt] = useState(Date.now());

  useEffect(() => {
    async function onClick(ev: MouseEvent) {
      const container = ref.current;
      const target = ev.target;
      await setTimeout();
      if (
        container !== null &&
        target instanceof Node &&
        !ev.defaultPrevented &&
        Date.now() - mountedAt >= waitFor &&
        (container === target || !container.contains(target))
      ) {
        onClickAway(ev);
      }
    }
    document.body.addEventListener('click', onClick, true);
    return () => document.body.removeEventListener('click', onClick);
  }, [mountedAt, onClickAway, waitFor]);

  return <div ref={ref}>{children}</div>;
};
