import React, { useEffect, useState } from 'react';

import { FindTour } from '@enactive-ac/api/src/domain/FindTour';

import { Analytics } from './components/Analytics';
import { ThemeContainer } from './components/ThemeContainer';
import { TourSequence } from './components/TourSequence';
import { useMemoAsync } from './hooks/useMemoAsync';
import { useTourService } from './services/DependenciesProvider';
import { useEnactiveMetadata } from './services/MetadataProvider';

const App: React.FC = () => {
  const metadata = useEnactiveMetadata();
  const tourService = useTourService();
  const [availableTours] = useMemoAsync(
    () => tourService.findAvailableTours(metadata.context),
    [metadata, tourService],
    [],
  );

  const [canLaunchTour, setCanLaunchTour] = useState(true);
  const [launchedTour, setLaunchedTour] = useState<FindTour>();

  useEffect(() => {
    if (canLaunchTour) {
      setLaunchedTour(
        availableTours
          .filter(t => t.launch)
          .sort((t0, t1) => t0.steps.length - t1.steps.length)[0],
      );
    }
  }, [availableTours, canLaunchTour, launchedTour]);

  useEffect(() => {
    if (launchedTour !== undefined) {
      setCanLaunchTour(false);
    }
  }, [launchedTour]);

  return (
    <>
      <Analytics />
      {availableTours.length > 0 ? (
        <ThemeContainer>
          {launchedTour !== undefined ? (
            <TourSequence
              onAction={(stepId, _, action) =>
                tourService.collectTourAction(
                  metadata.context,
                  launchedTour.id,
                  stepId,
                  action,
                )
              }
              onComplete={() => setLaunchedTour(undefined)}
              tour={launchedTour.steps}
            />
          ) : null}
        </ThemeContainer>
      ) : null}
    </>
  );
};

export default App;
