/* eslint-disable no-console */

export function attributesToObject(
  entries: IterableIterator<[string, string]>,
): Record<string, unknown> {
  const result: Record<string, unknown> = {};
  for (const [key, value] of entries) {
    const [init, last] = parseFlattenedKeys(key);
    let level = result;
    for (const part of init) {
      if (part in level && typeof level[part] !== 'object') {
        console.warn(
          'Assigned both a value and an object on enactive script tag metadata',
          { key, value: level[part] },
        );
      }

      if (part in level) {
        level = level[part] as Record<string, unknown>;
      } else {
        level = level[part] = {};
      }
    }

    if (last in level) {
      console.warn(
        'Assigned duplicate values on enactive script tag metadata',
        { key, value },
      );
    }
    level[last] = coerceValue(value);
  }
  return result;
}

/**
 * Perform a simple coercion on attribute values that hopefully works as
 * predictably as possible, and is as easy to explain as possible.
 *
 * In the process of parsing our enactive metadata, we do have ajv where we
 * can (and do) apply a level of coercion against a JSONSchema. However, for
 * custom context attributes, there is no schema to infer intention.
 */
function coerceValue(value: string): unknown {
  if (value === '') {
    // Empty string means true (e.g., attribute specified with no value). This
    // is fairly idiomatic
    return true;
  } else if (value === 'undefined') {
    // The text "undefined" we'll just say is undefined (e.g., the value will
    // not be sent). This is maybe a bit less idiomatic but is bijective with
    // how JSON.stringify(...) would work with something that had value
    // undefined
    return undefined;
  } else {
    /**
     * If we don't have a reserved word, then attempt to parse as JSON. If
     * parsing fails, then we fall back to a string.
     *
     * Examples:
     *
     * enactive.context.attribute="true" => true
     * enactive.context.attribute="null" => null
     * enactive.context.attribute="\"undefined\"" => 'undefined'
     * enactive.context.attribute="\"true\"" => 'true'
     * enactive.context.attribute="12.4" => 12.4
     * enactive.context.attribute="this is not even json" => 'this is not even json'
     */
    try {
      return JSON.parse(value);
    } catch {
      return value;
    }
  }
}

function parseFlattenedKeys(key: string): [string[], string] {
  const init = splitDot(key).map(sausageToCamel);
  const last = init.pop() ?? '';
  return [init, last];
}

function splitDot(str: string): string[] {
  return (
    str
      // Trim dots
      .replaceAll(/^\.+|\.+$/g, '')
      // Reduce multiple dots
      .replaceAll(/\.+/g, '.')
      .split('.')
  );
}

function sausageToCamel(str: string): string {
  // Convert sausage-case-keys to camelCaseKeys
  return (
    str
      // Trim dashes
      .replaceAll(/^-+|-+$/g, '')
      // De-duplicate successive dashes
      .replaceAll(/-+/g, '-')
      .split('-')
      .map((w, i) =>
        i > 0 && w.length > 0
          ? `${w[0].toUpperCase()}${w.slice(1).toLowerCase()}`
          : w.toLowerCase(),
      )
      .join('')
  );
}
