import React, { useCallback, useMemo } from 'react';

import { useWindowEventListener } from '../hooks/useWindowEventListener';
import { useEventsService } from '../services/DependenciesProvider';
import { useSite } from '../services/InitService';
import { useEnactiveMetadata } from '../services/MetadataProvider';

export const Analytics: React.FC = () => {
  const metadata = useEnactiveMetadata();
  const site = useSite();
  const eventsService = useEventsService();

  // Determine which element attributes we're monitoring
  const { monitors, monitorSelector } = useMemo(() => {
    const monitors = site.platform?.settings.monitors ?? [];
    const monitorSelector =
      monitors.length > 0
        ? monitors.map(attrName => `[${attrName}]`).join(',')
        : undefined;
    return { monitorSelector, monitors };
  }, [site]);

  const handleClick = useCallback<EventListener>(
    async ev => {
      const target = ev.target;
      if (
        target !== null &&
        target instanceof Element &&
        monitorSelector !== undefined
      ) {
        const key = findMonitorValue(
          target.matches(monitorSelector)
            ? target
            : target.closest(monitorSelector),
          monitors,
        );
        if (key !== undefined) {
          await eventsService.collectClickEvent(key, metadata.context);
        }
      }
    },
    [eventsService, metadata, monitors, monitorSelector],
  );
  useWindowEventListener('click', handleClick, {
    capture: true,
    passive: true,
  });

  return null;
};

function findMonitorValue(
  el: Element | null,
  monitors: string[],
): string | undefined {
  if (el === null) {
    return undefined;
  }

  for (const m of monitors) {
    const value = el.getAttribute(m);
    if (value !== null) {
      return value;
    }
  }
}
