import React, { useEffect, useMemo, useState } from 'react';

import { TourStep } from '@enactive-ac/api/src/domain/TourStep';
import { TourStepButton } from '@enactive-ac/api/src/domain/TourStepButton';

import { ClickAwayListener } from './ClickAwayListener';
import { Mask } from './Mask';
import { TourStepPopup } from './TourStepPopup';

export type TourSequenceProps = {
  onAction: (
    stepId: string,
    stepIndex: number,
    action: TourStepButton['action'],
  ) => void;
  onComplete: () => void;
  tour: TourStep[];
};

export const TourSequence: React.FC<TourSequenceProps> = ({
  onAction,
  onComplete,
  tour,
}) => {
  const [stepIndex, setStepIndex] = useState<number>(-1);
  const [mutationCount, setMutationCount] = useState<number>(0);

  const stepInfos = useMemo(() => {
    return tour.map(step => ({
      step,
      targetEl: ((): HTMLElement | null | undefined => {
        switch (step.selector?.type) {
          case 'css':
            return document.querySelector<HTMLElement>(step.selector.value);
          case 'xpath':
            return document.evaluate(
              step.selector.value,
              document,
              null,
              XPathResult.FIRST_ORDERED_NODE_TYPE,
              null,
            ).singleNodeValue as HTMLElement | null;
          case undefined:
            return undefined;
        }
      })(),
    }));
    // We're only using mutationCount to kick a reload of the step infos based
    // on mutation observer events
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tour, mutationCount]);
  const canStart = useMemo(
    () =>
      // We don't want to start this tour until all elements are available. In
      // the future maybe we just go step-by-step waiting for each subsequent
      // element (e.g., part of a tour continuation)
      stepInfos.every(({ targetEl }) => {
        if (targetEl === undefined) {
          // Target element is not appropriate for this tour
          return true;
        }

        // Non-existing or no offset parent (decent check for visibility)
        if (targetEl === null || targetEl.offsetParent === null) {
          return false;
        }

        // Hidden via position or no inner content loaded yet
        const { width, height, x, y } = targetEl.getBoundingClientRect();
        if (width === 0 || height === 0 || x < 0 || y < 0) {
          return false;
        }

        // Style-based visibility, kind of a slow check so do it last
        const style = window.getComputedStyle(targetEl);
        if (style.visibility === 'hidden' || style.display === 'none') {
          return false;
        }

        // This element is loaded and available on the DOM
        return true;
      }),
    [stepInfos],
  );

  useEffect(() => {
    if (!canStart) {
      // We set a mutation count just to trigger a refresh of this node to check
      // again if we can start
      const handler = setInterval(() => setMutationCount(mc => mc + 1), 1000);
      return () => clearInterval(handler);
    } else {
      // Wait an additional second before we start the next tour
      const timeoutHandler = setTimeout(() => setStepIndex(0), 1000);
      return () => clearTimeout(timeoutHandler);
    }
  }, [canStart]);

  useEffect(() => {
    const el = stepInfos[stepIndex]?.targetEl;
    if (el !== undefined && el !== null) {
      const rect = el.getBoundingClientRect();
      const isInViewport =
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <= window.innerHeight &&
        rect.right <= window.innerWidth;
      if (!isInViewport) {
        el.scrollIntoView({
          behavior: 'smooth',
          block: 'center',
          inline: 'center',
        });
      }
    }
  }, [stepInfos, stepIndex]);

  return canStart ? (
    <>
      <Mask
        active={stepInfos[stepIndex]?.step.mask ?? false}
        expose={stepInfos[stepIndex]?.targetEl ?? undefined}
      />
      <ClickAwayListener
        onClickAway={() => {
          const stepId = stepInfos[stepIndex]?.step.id;
          if (stepId !== undefined) {
            onAction(stepId, stepIndex, 'close');
          }
          setStepIndex(-1);
        }}
      >
        {stepInfos.map(({ step, targetEl }, i) => {
          return (
            <div key={step.id}>
              <TourStepPopup
                active={stepIndex === i}
                buttons={
                  step.buttons ??
                  [
                    { action: 'dismiss', text: 'Dismiss' },
                    i < tour.length - 1
                      ? { action: 'next', text: 'Next' }
                      : undefined,
                  ].filter((b): b is TourStepButton => b !== undefined)
                }
                html={step.html}
                onAction={action => {
                  onAction(step.id, i, action);
                  switch (action) {
                    case 'close':
                    case 'dismiss':
                      return setStepIndex(-1);
                    case 'next':
                      return setStepIndex(i + 1);
                  }
                }}
                onOut={() => {
                  if (stepIndex === -1) {
                    onComplete();
                  }
                }}
                targetEl={targetEl ?? undefined}
              />
            </div>
          );
        })}
      </ClickAwayListener>
    </>
  ) : (
    <></>
  );
};
