import React from 'react';

import 'typeface-roboto';

import { createRoot } from 'react-dom/client';

import App from './App';
import { DependenciesProvider } from './services/DependenciesProvider';
import { InitProvider } from './services/InitService';
import { MetadataProvider } from './services/MetadataProvider';

const rootEl = document.getElementById('enactive-app-root');
if (rootEl === null) {
  throw new Error(
    'Failed to initialize because there was no #enactive-app-root element',
  );
}

const root = createRoot(rootEl);
root.render(
  <MetadataProvider>
    <DependenciesProvider>
      <InitProvider>
        <App />
      </InitProvider>
    </DependenciesProvider>
  </MetadataProvider>,
);
