import React, {
  CSSProperties,
  PropsWithChildren,
  useEffect,
  useMemo,
  useRef,
} from 'react';

import { autoUpdate, useFloating } from '@floating-ui/react-dom';
import { styled } from '@mui/material/styles';
import { CSSTransition } from 'react-transition-group';

export type MaskProps = PropsWithChildren<{
  active: boolean;
  // An element to expose in the mask
  expose?: HTMLElement;
  onOutEnd?: () => void;
  ref?: React.RefCallback<HTMLElement>;
  style?: CSSProperties;
}>;

const exposePadding = 10;

const MaskInner = styled('div')({
  '&, &.enter, &.exit.exit-active, &.exit-done': {
    opacity: 0,
    pointerEvents: 'none',
  },
  '&.enter-active, &.exit-active': {
    transition: 'opacity 250ms linear',
  },
  '&.enter.enter-active, &.enter-done, &.exit': {
    opacity: 1,
    pointerEvents: 'auto',
  },
  bottom: 0,
  left: 0,
  position: 'fixed',
  right: 0,
  top: 0,
  zIndex: 1111,
});

export const Mask: React.FC<MaskProps> = ({
  active,
  children,
  expose,
  onOutEnd = () => undefined,
  ref,
}) => {
  const maskRef = useRef<HTMLElement | null>(null);

  const { x, y, floating, reference } = useFloating({
    // We just need deterministic placement so we can subtract the height from
    // y afterwards to make it a direct overlay
    placement: 'bottom',
    strategy: 'absolute',
    whileElementsMounted: autoUpdate,
  });

  useEffect(() => reference(expose ?? null), [expose, reference]);

  // The rectangle around the expose element. Used to get sizing information
  // that feeds into FloatingUI
  const elRect = useMemo(() => expose?.getBoundingClientRect(), [expose]);

  // The rectangle dimensions that will be the "hole" in the mask
  const exposeRect = useMemo(() => {
    if (elRect === undefined) {
      return undefined;
    }

    return {
      height: elRect.height + exposePadding * 2,
      width: elRect.width + exposePadding * 2,
      x: (x ?? 0) - exposePadding,
      y: (y ?? 0) - elRect.height - exposePadding,
    };
  }, [x, y, elRect]);

  return (
    <CSSTransition
      in={active}
      nodeRef={maskRef}
      onExited={onOutEnd}
      timeout={250}
    >
      <MaskInner
        ref={(el: HTMLElement | null) => {
          maskRef.current = el;
          ref?.(el);
        }}
      >
        {elRect !== undefined ? (
          <div
            ref={floating}
            style={{
              height: elRect.height,
              pointerEvents: 'none',
              position: 'absolute',
              visibility: 'hidden',
              width: elRect.width,
            }}
          />
        ) : undefined}
        <svg height="100%" width="100%">
          <defs>
            <mask id="maskMask">
              <rect fill="white" height="100%" width="100%" />
              {exposeRect !== undefined ? (
                <rect fill="black" rx={5} ry={5} {...exposeRect} />
              ) : undefined}
            </mask>
          </defs>
          <rect
            fill="black"
            fillOpacity={0.7}
            height="100%"
            mask="url(#maskMask)"
            width="100%"
          />
        </svg>
        {children}
      </MaskInner>
    </CSSTransition>
  );
};
