import { EventContext } from '@enactive-ac/api/src/domain/EventContext';
import { PostClickEventRequest } from '@enactive-ac/api/src/domain/PostEvent';

import { resolveEventContext } from '../domain/EnactiveMetadata';
import { RequestConfig } from '../domain/RequestConfig';
import { RecursivePartial } from '../util/RecursivePartial';

export class EventsService {
  constructor(private readonly requestConfig: RequestConfig) {}

  public async collectClickEvent(
    target: string,
    context: RecursivePartial<EventContext> | undefined,
  ): Promise<void> {
    const req: PostClickEventRequest = {
      context: resolveEventContext(context),
      target,
    };
    await fetch(`${this.requestConfig.baseUrl}/events/click`, {
      body: JSON.stringify(req),
      headers: {
        'content-type': 'application/json',
      },
      /**
       * If the request caused navigation, then this will allow the request
       * to survive the navigation.
       */
      keepalive: true,
      method: 'post',
    });
  }
}
