import { styled } from '@mui/system';

const fontFamily = '"Roboto", "Helvetica", "Arial", sans-serif';
const fontSize = '16px';

/**
 * Create a bit of a class-based component library for tours that can be easily
 * overridden on the client side.
 *
 * The Theme will carry dynamic props (e.g., colors) that were configured from
 * the management server-side. But then the class names for specific components
 * should be coherent enough for more advanced branding on the customer site.
 */
export const ThemeContainer = styled('div')(({ theme }) => ({
  ...{
    [`&, ${['button', 'p', 'ul', 'ol', 'dl'].map(t => `& ${t}`).join(',')}`]: {
      color: 'rgba(0, 0, 0, 0.87)',
      fontFamily,
      fontSize: '16px',
    },
  },

  /** Base button styles */
  ...{
    '& button': {
      fontFamily,
      fontSize,
      /** Structural */
      ...{
        alignContent: 'center',
        alignItems: 'center',
        borderRadius: '0.4em',
        cursor: 'pointer',
        display: 'inline-flex',
        flexDirection: 'row',
        justifyContent: 'center',
        lineHeight: '1.5em',
        margin: 0,
        minWidth: '5em',
        overflow: 'hidden',
        padding: '0.6em 0.8em',
        position: 'relative',
        transition: 'background 100ms linear',
        zIndex: 1,
      },
      /** Reset */
      background: 'rgba(0, 0, 0, 0)',
      border: 'none',
      boxShadow: 'none',
    },
    '& button::before': {
      background: '#000',
      bottom: 0,
      content: '""',
      left: 0,
      opacity: 0,
      position: 'absolute',
      right: 0,
      top: 0,
      zIndex: -1,
    },
    '& button:active::before, & button:hover::before, & button:focus::before': {
      opacity: 0.07,
    },
    '& button:disabled': {
      cursor: 'default',
      opacity: 0.5,
      pointerEvents: 'none',
    },
    '& div.enactive-buttongroup': {
      margin: '-0.5em',
    },
    '& div.enactive-buttongroup button': {
      margin: '0.5em',
    },
  },

  /** Contained button styles: button.enactive-contained */
  ...{
    '& button.enactive-contained': {
      boxShadow:
        '0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12)',
    },
    '& button.enactive-contained::before': {
      opacity: 0.07,
    },
    '& button.enactive-contained:active::before, & button.enactive-contained:hover::before, & button.enactive-contained:focus::before':
      {
        opacity: 0,
      },
  },

  /** Primary button styles: button.enactive-primary */
  ...{
    '& button.enactive-primary': {
      color: theme.palette.primary.contrastText,
    },
    '& button.enactive-primary::before': {
      background: theme.palette.primary.main,
      opacity: 1,
    },
    '& button.enactive-primary:active::before, & button.enactive-primary:hover::before, & button.enactive-primary:focus::before':
      {
        opacity: 0.85,
      },
  },

  /** Paragraph styles */
  '& p': {
    fontFamily,
    fontSize,
    lineHeight: 1.4,
    margin: 0,
  },
}));
