import { useEffect, useState } from 'react';

export function useMemoAsync<M, E = unknown>(
  f: () => Promise<M> | M,
  deps: unknown[],
): [M | undefined, E | undefined];

export function useMemoAsync<M, E = unknown>(
  f: () => Promise<M> | M,
  deps: unknown[],
  defaultValue: M,
): [M, E | undefined];

export function useMemoAsync<M, E = unknown>(
  f: () => Promise<M> | M,
  deps: unknown[],
  defaultValue?: M,
): [M | typeof defaultValue, E | undefined] {
  const [value, setValue] = useState(defaultValue);
  const [error, setError] = useState<E>();

  useEffect(() => {
    void (async () => {
      try {
        const value = await f();
        setValue(() => value);
        setError(undefined);
      } catch (error) {
        setValue(() => undefined);
        setError(() => error as E);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, deps);

  return [value, error];
}
