import React, {
  ReactNode,
  useEffect,
  useState,
  useRef,
  PropsWithChildren,
} from 'react';

import { styled } from '@mui/system';
import { CSSTransition } from 'react-transition-group';

import { FlexColumn, FlexContent, FlexRow } from './Flex';
import { Popper } from './Popper';

export type PopupProps = PropsWithChildren<{
  anchorEl?: HTMLElement;
  buttons?: ReactNode[];
  onOutEnd?: () => void;
  open: boolean;
}>;

type ContentProps = {
  canScrollDown: boolean;
  canScrollUp: boolean;
};

const Content = styled(FlexContent, {
  shouldForwardProp: prop => prop !== 'canScrollDown' && prop !== 'canScrollUp',
})<ContentProps>(({ canScrollDown, canScrollUp }) => ({
  boxShadow: [
    `inset 0 -15px 15px -20px rgba(0, 0, 0, ${canScrollDown ? 0.75 : 0})`,
    `inset 0 15px 15px -20px rgba(0, 0, 0, ${canScrollUp ? 0.75 : 0})`,
  ].join(', '),
  maxWidth: '25em',
  minWidth: '20em',
  overflow: 'auto',
  padding: '1em',
  transition: 'box-shadow 0.3s ease-in-out',
}));

const Buttons = styled(FlexRow)({
  justifyContent: 'flex-end',
  padding: '1em',
});

const Systray = styled('div')({
  bottom: '2em',
  // Ensures children can set a max-height: 100% without putting explicit
  // height on this systray container
  display: 'flex',
  height: 'auto',
  maxHeight: '80%',
  maxWidth: '35em',
  position: 'fixed',
  right: '2em',
});

const Inner = styled(FlexColumn)({
  backgroundColor: 'white',
  borderRadius: '1em',
  maxHeight: '100%',
  overflow: 'hidden',
  position: 'relative',
});

const TransitionSlideFade = styled('div')({
  '&, &.enter, &.exit.exit-active, &.exit-done': {
    opacity: 0,
    top: '2em',
  },
  '&.enter-active, &.exit-active': {
    transition: 'opacity 250ms ease-in, top 250ms ease-in',
  },
  '&.enter.enter-active, &.enter-done, &.exit': {
    opacity: 1,
    top: 0,
  },
  '[data-popper-placement="bottom"] &::before': {
    left: 'calc(50% - 0.5em)',
    top: '-0.5em',
  },
  '[data-popper-placement="left"] &::before': {
    right: '-0.5em',
    top: 'calc(50% - 0.5em)',
  },
  '[data-popper-placement="right"] &::before': {
    left: '-0.5em',
    top: 'calc(50% - 0.5em)',
  },
  '[data-popper-placement="top"] &::before': {
    bottom: '-0.5em',
    left: 'calc(50% - 0.5em)',
  },
  '[data-popper-placement] &::before': {
    backgroundColor: 'white',
    boxShadow:
      '0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12)',
    content: '""',
    display: 'block',
    height: '1em',
    position: 'absolute',
    transform: 'rotate(45deg)',
    width: '1em',
  },
  borderRadius: '1em',
  boxShadow:
    '0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12)',
  position: 'relative',
});
export const Popup: React.FC<PopupProps> = ({
  anchorEl,
  buttons = [],
  children,
  onOutEnd = () => undefined,
  open,
}) => {
  const transitionRef = useRef(null);
  const [popperActive, setPopperActive] = useState(open);
  const [popperClosed, setPopperClosed] = useState(false);

  const [contentEl, setContentEl] = useState<HTMLDivElement>();
  const [canScrollDown, setCanScrollDown] = useState(false);
  const [canScrollUp, setCanScrollUp] = useState(false);

  useEffect(() => {
    if (open && !popperActive && !popperClosed) {
      setPopperActive(true);
    } else if (popperClosed) {
      setPopperActive(false);
      setPopperClosed(false);
    }
  }, [open, popperActive, popperClosed]);

  useEffect(() => {
    if (contentEl !== undefined) {
      function onScroll(this: HTMLElement) {
        if (this.scrollTop > 10) {
          setCanScrollUp(true);
        } else {
          setCanScrollUp(false);
        }

        if (this.scrollHeight - this.scrollTop - this.clientHeight > 10) {
          setCanScrollDown(true);
        } else {
          setCanScrollDown(false);
        }
      }

      // Do an initial scroll boundaries check to set initial box shadows
      onScroll.apply(contentEl);

      contentEl.addEventListener('scroll', onScroll);
      return () => contentEl.removeEventListener('scroll', onScroll);
    }
  }, [contentEl]);

  return anchorEl !== undefined ? (
    <Popper
      anchorEl={anchorEl}
      offset={30}
      open={popperActive}
      placement="auto"
      style={{ zIndex: 1112 }}
    >
      <CSSTransition
        in={open}
        nodeRef={transitionRef}
        onExited={() => {
          setPopperClosed(true);
          onOutEnd();
        }}
        timeout={250}
      >
        <TransitionSlideFade ref={transitionRef}>
          <Inner>
            <Content
              ref={el => setContentEl(el ?? undefined)}
              canScrollDown={canScrollDown}
              canScrollUp={canScrollUp}
            >
              {children}
            </Content>
            {buttons.length > 0 ? (
              <Buttons className="enactive-buttongroup">{buttons}</Buttons>
            ) : undefined}
          </Inner>
        </TransitionSlideFade>
      </CSSTransition>
    </Popper>
  ) : (
    <Systray
      style={{
        zIndex: 1112,
        ...(!open ? { pointerEvents: 'none' } : undefined),
      }}
    >
      <CSSTransition
        in={open}
        nodeRef={transitionRef}
        onExited={() => {
          setPopperClosed(true);
          onOutEnd();
        }}
        timeout={250}
      >
        <TransitionSlideFade ref={transitionRef}>
          <Inner>
            <Content
              ref={el => setContentEl(el ?? undefined)}
              canScrollDown={canScrollDown}
              canScrollUp={canScrollUp}
            >
              {children}
            </Content>
            {buttons.length > 0 ? (
              <Buttons className="enactive-buttongroup">{buttons}</Buttons>
            ) : undefined}
          </Inner>
        </TransitionSlideFade>
      </CSSTransition>
    </Systray>
  );
};
