import React from 'react';

import { InitResponse } from '@enactive-ac/api/src/domain/Init';
import { DefaultTheme, ThemeProvider } from '@mui/system';
import { AxiosInstance } from 'axios';

import { useMemoAsync } from '../hooks/useMemoAsync';
import { useInitService } from './DependenciesProvider';

export class InitService {
  constructor(private readonly axios: AxiosInstance) {}

  async init(): Promise<InitResponse> {
    return (await this.axios.get<InitResponse>('init')).data;
  }
}

const InitContext = React.createContext<InitResponse>(
  undefined as unknown as InitResponse,
);

export type InitProviderProps = {
  children: React.ReactNode;
};
export const InitProvider: React.FC<InitProviderProps> = ({ children }) => {
  const initService = useInitService();
  const [init] = useMemoAsync(() => initService.init(), [initService]);
  const theme = React.useMemo<DefaultTheme>(
    () => ({
      palette: {
        primary: {
          contrastText: init?.site.theme?.primaryContrastText ?? '#fff',
          main: init?.site.theme?.primaryColor ?? '#4f4f4f',
        },
      },
    }),
    [init],
  );
  if (init !== undefined) {
    return (
      <InitContext.Provider value={init}>
        <ThemeProvider theme={theme}>{children}</ThemeProvider>
      </InitContext.Provider>
    );
  } else {
    return null;
  }
};

export function useSite() {
  return React.useContext(InitContext).site;
}
