import React, { createContext, ReactNode, useContext, useMemo } from 'react';

import axiosStatic, { AxiosInstance } from 'axios';

import { RequestConfig } from '../domain/RequestConfig';
import { EventsService } from './EventsService';
import { InitService } from './InitService';
import { useEnactiveMetadata } from './MetadataProvider';
import { TourService } from './TourService';

export type Dependencies = {
  axios: AxiosInstance;
  eventsService: EventsService;
  requestConfig: RequestConfig;
  tourService: TourService;
  initService: InitService;
};
const DependenciesContext = createContext<Dependencies>(
  undefined as unknown as Dependencies,
);

export type DependenciesProviderProps = {
  children: ReactNode;
};
export const DependenciesProvider: React.FC<DependenciesProviderProps> = ({
  children,
}) => {
  const { apiHost, siteId } = useEnactiveMetadata();
  const requestConfig = useMemo<RequestConfig>(
    () => ({ baseUrl: `https://${apiHost}/api/sites/${siteId}` }),
    [apiHost, siteId],
  );
  const axios = useMemo(
    () =>
      axiosStatic.create({
        baseURL: requestConfig.baseUrl,
      }),
    [requestConfig],
  );
  const initService = useMemo(() => new InitService(axios), [axios]);
  const eventsService = useMemo(
    () => new EventsService(requestConfig),
    [requestConfig],
  );
  const tourService = useMemo(() => new TourService(axios), [axios]);
  const dependencies = useMemo(
    () => ({
      axios,
      eventsService,
      initService,
      requestConfig,
      tourService,
    }),
    [requestConfig, axios, initService, eventsService, tourService],
  );
  return (
    <DependenciesContext.Provider value={dependencies}>
      {children}
    </DependenciesContext.Provider>
  );
};

export function useInitService() {
  return useContext(DependenciesContext).initService;
}

export function useEventsService() {
  return useContext(DependenciesContext).eventsService;
}

export function useTourService() {
  return useContext(DependenciesContext).tourService;
}
